 .navbar {
     width: 100%;
     height: 40px;
     position: fixed;
     display: flex;
     justify-content: space-between;
     background-color:rgb(63, 63, 199);
     padding: 20px;
     align-items: center;
     color: white;
 }

 .logo {
     color: white;
     font-size: 30px;
     font-weight: 400;
     font-style:oblique;

 }

 .links {
     color: white;
     display: flex;
     justify-content: flex-end;
     cursor: pointer;
     list-style: none;


 }

 .about,.contact,.skill,.project {
     text-decoration: none;
     color: white;
     font-size: 20px;
     padding: 10px 20px;
     margin: 0 10px;
     cursor: pointer;
 }



 .mobile_icon {
 display: none;
 }


 @media only screen and (max-width:380px) {
    .link_mobile {
        position: absolute;
         top: 75px;
         left: 0;
         list-style: none;
         width: 100%;
         display:block;
         transition: all 0.5s ease-out;
         background-color:rgb(149, 149, 226);
        
     }

     .about,.contact,.skill,.project {
         color: white;
         width: 100%;
         text-align:center;
         transition: all 0.5s ease-out;
     }

     .logo {
         display: flex;
         position: absolute;
         top: 15px;
         left: 35px;
         text-align: center;
         font-size: 20px;
     }

     .mobile_icon {

         display: block;
         position: absolute;
         font-size: 30px;
         color: white;
         background-color: teal;
         border: none;
         outline: none;
         top: 28px;
         right: 50px;
     }

 .links {
         color: white;
         display: none;
         cursor: pointer;
         list-style: none;
     } 


 }

